  pendingJump: boolean;
  // сколько препятствий прошло с момента последнего появления Онигири
  sinceLastOnigiriCount: number;
  // coin independent spawner
  coinSpawnTimerSec: number;
  coinNextSpawnSec: number;

  // visuals
  skyColor: string;
  // debug
  debugDrawHitboxes?: boolean;

  // player sprite/atlas
  playerSpriteUrl: string;
  playerAtlasUrl: string;
  playerSprite: ImageBitmap | null;
  playerAtlas: PlayerAtlas | null;
  playerSpriteLoading: boolean;
  playerRunFrames: string[];
  playerJumpFrames: string[];
  playerHitFrames: string[];
  playerRewardFrames: string[];
  // death animation frames
  playerDyingFrames: string[];
  blightFrames: string[];
  heartFrames: string[];
  coinFrameName: string | null;
  coinFrames: string[];
  blightAnimElapsedMs: number;
  blightAnimFrameIndex: number;
  skeletonFrames: string[];
  heartAnimElapsedMs: number;
  heartAnimFrameIndex: number;
  coinAnimElapsedMs: number;
  coinAnimFrameIndex: number;
  skeletonAnimElapsedMs: number;
  skeletonAnimFrameIndex: number;
  playerAnimFps: number;
  playerAnimFrameMs: number;
  playerAnimElapsedMs: number;
  playerAnimFrameIndex: number;
  // run phase preservation across jump
  playerRunStartIndexAtJump: number;
  playerRunPhaseMsDuringJump: number;
  // jump timing (for time-based frame selection)
  playerJumpElapsedMs: number;
  playerJumpTotalMs: number;
  // hit one-shot state
  playerHitPlaying: boolean;
  playerHitElapsedMs: number;
  playerHitFrameIndex: number;
  // reward one-shot state
