export function applyCanvasSize(s: GameState, newCssW: number, newCssH: number, dpr: number): void {
  s.cssW = Math.max(1, Math.floor(newCssW));
  s.cssH = Math.max(1, Math.floor(newCssH));

  // Адаптивный кламп DPR: учитываем капы буфера, размер контейнера и производительность
  const rawDpr = Math.max(dpr || 1, 1);
  const areaCss = Math.max(1, s.cssW * s.cssH);
  const maxAreaPx = MAX_W_PX * MAX_H_PX;
  const dprByCaps = Math.max(1, Math.sqrt(maxAreaPx / areaCss));
  const perfCap = (s.cssW >= 1024 || s.cssH >= 1024) ? 2 : 3;
  const quantize = (x: number) => Math.max(1, Math.round(x / 0.25) * 0.25);
  const dprClamped = quantize(Math.min(rawDpr, dprByCaps, perfCap));

  const sW = MAX_W_PX / s.cssW;
  const sH = MAX_H_PX / s.cssH;

  let bound: 'dpr' | 'w' | 'h' = 'dpr';
  let uniform = dprClamped;
  if (sW < uniform) { uniform = sW; bound = 'w'; }
  if (sH < uniform) { uniform = sH; bound = 'h'; }
  s.scale = Math.max(1 / 8, uniform);

  let targetWpx = 0;
  let targetHpx = 0;
  if (bound === 'h') {
    targetHpx = Math.max(1, Math.floor(s.cssH * s.scale));
    targetWpx = Math.max(1, Math.round(targetHpx * (s.cssW / s.cssH)));
  } else if (bound === 'w') {
    targetWpx = Math.max(1, Math.floor(s.cssW * s.scale));
    targetHpx = Math.max(1, Math.round(targetWpx * (s.cssH / s.cssW)));
  } else {
    targetWpx = Math.max(1, Math.round(s.cssW * s.scale));
    targetHpx = Math.max(1, Math.round(s.cssH * s.scale));
  }

  if (!s.canvas || !s.ctx) return;

  if (s.canvas.width !== targetWpx) s.canvas.width = targetWpx;
  if (s.canvas.height !== targetHpx) s.canvas.height = targetHpx;



  s.player.x = s.cssW * 0.14;
}

export { GAME_HEIGHT, groundY };


