import type { GameState } from './state';
import { GAME_HEIGHT, groundY, speedX } from './constants';
import { playerConfig, entitiesConfig, GROUND_BOTTOM_MARGIN_CSS } from '@/config/visual';
import { ensurePlayerSprite } from './assets';


export function draw(s: GameState, alpha: number = 1, stepSec: number = 1 / 60): void {
  if (!s.ctx || !s.canvas) return;

  // Clear buffer
  if (s.transparentBg) {
    s.ctx.clearRect(0, 0, s.canvas.width, s.canvas.height);
  } else {
    s.ctx.fillStyle = s.skyColor;
    s.ctx.fillRect(0, 0, s.canvas.width, s.canvas.height);
  }

  // Зафиксировать линию земли на GROUND_BOTTOM_MARGIN_CSS от низа холста:
  // вычисляем вертикальное смещение так, чтобы top земли (groundY) попадал в cssH - GROUND_BOTTOM_MARGIN_CSS.
  const groundTopCss = s.cssH - GROUND_BOTTOM_MARGIN_CSS;
  const offsetYpx = Math.round((groundTopCss - groundY) * s.scale);

  // (Ground removed)

  // Player
  const playerYInterp = s.prevPlayerY + (s.player.y - s.prevPlayerY) * alpha;
  ensurePlayerSprite(s);
  if (s.playerSprite && s.playerAtlas) {
    // Выбор кадра по приоритету: dying → reward → hit → jump → run
    let chosenName: string | null = null;
    if (s.isDying && s.playerDyingFrames && s.playerDyingFrames.length > 0) {
      chosenName = s.playerDyingFrames[Math.max(0, Math.min(s.playerDyingFrames.length - 1, s.playerDyingFrameIndex))] || null;
    } else if (s.playerRewardPlaying && s.playerRewardFrames.length > 0) {
      chosenName = s.playerRewardFrames[Math.max(0, Math.min(s.playerRewardFrames.length - 1, s.playerRewardFrameIndex))] || null;
    } else if (s.playerHitPlaying && s.playerHitFrames.length > 0) {
      chosenName = s.playerHitFrames[Math.max(0, Math.min(s.playerHitFrames.length - 1, s.playerHitFrameIndex))] || null;
    } else if (s.player.isJumping && s.playerJumpFrames.length > 0) {
      // В начале прыжка коротко показываем кадр бега, с которого стартовали
      const total = s.playerJumpTotalMs || 1;
      const pRaw = Math.max(0, Math.min(1, s.playerJumpElapsedMs / total));
      const holdStart = 0.1; // 10% времени прыжка — стартовый кадр бега
      if (pRaw < holdStart && s.playerRunFrames.length > 0) {
        chosenName = s.playerRunFrames[Math.max(0, Math.min(s.playerRunFrames.length - 1, s.playerRunStartIndexAtJump))];
      } else {
        // Равномерное распределение по оставшемуся времени
        const p = Math.max(0, Math.min(1, (pRaw - holdStart) / (1 - holdStart)));
        const n = s.playerJumpFrames.length;
        const idx = Math.max(0, Math.min(n - 1, Math.floor(p * (n - 1) + 1e-6)));
        chosenName = s.playerJumpFrames[idx];
      }
    } else if (s.playerRunFrames.length > 0) {
      chosenName = s.playerRunFrames[Math.max(0, Math.min(s.playerRunFrames.length - 1, s.playerAnimFrameIndex))];
    }

    const frFull = chosenName ? s.playerAtlas.frames[chosenName] : undefined;
    const fr = frFull?.frame;
    const pivot = frFull?.pivot || { x: 0.5, y: 1 };
    if (fr) {
      const destHcss = playerConfig.visualHeight;
      const destWcss = (fr.w / fr.h) * destHcss;
      const leftCss = s.player.x - pivot.x * destWcss + (playerConfig.offsetX || 0);
      const bottomCss = playerYInterp + s.player.height + (playerConfig.offsetY || 0);
      const topCss = bottomCss - destHcss * pivot.y;
      const dx = Math.round(leftCss * s.scale);
      const dy = Math.round((topCss) * s.scale + offsetYpx);
      const dw = Math.max(1, Math.round(destWcss * s.scale));
      const dh = Math.max(1, Math.round(destHcss * s.scale));
      const prevSmooth = s.ctx.imageSmoothingEnabled;
      s.ctx.imageSmoothingEnabled = false;
      s.ctx.drawImage(
        s.playerSprite,
        fr.x, fr.y, fr.w, fr.h,
        dx, dy, dw, dh
      );
      s.ctx.imageSmoothingEnabled = prevSmooth;
    }
  } else {
    s.ctx.fillStyle = '#000';
    s.ctx.fillRect(
      Math.round(s.player.x * s.scale),
      Math.round(playerYInterp * s.scale + offsetYpx),
      Math.round(s.player.width * s.scale),
      Math.round(s.player.height * s.scale)
    );
  }
  // Debug: player hitbox overlay
  if (s.debugDrawHitboxes) {
    const dx = Math.round(s.player.x * s.scale);
    const dy = Math.round(playerYInterp * s.scale + offsetYpx);
    const dw = Math.round(s.player.width * s.scale);
    const dh = Math.round(s.player.height * s.scale);
    s.ctx.save();
    s.ctx.globalAlpha = 0.3;
    s.ctx.fillStyle = '#ff00ff';
    s.ctx.fillRect(dx, dy, dw, dh);
    s.ctx.globalAlpha = 1;
    s.ctx.lineWidth = Math.max(1, Math.round(s.scale));
    s.ctx.strokeStyle = '#ff00ff';
    s.ctx.strokeRect(dx, dy, dw, dh);
    s.ctx.restore();
  }

  // Entities (не рисуем во время анимации смерти игрока)
  if (!s.isDying) for (const obs of s.entities) {
    const obsXInterp = obs.x + speedX * stepSec * (1 - alpha);
    const dxCss = obsXInterp;
    const dyCss = obs.y;
    // если есть атласные спрайты — рисуем их, иначе fallback прямоугольником
    if (s.playerSprite && s.playerAtlas && (obs.kind === 'blight' || obs.kind === 'skeleton' || obs.kind === 'heart' || obs.kind === 'coin')) {
      const name =
        obs.kind === 'blight'
          ? (s.blightFrames.length > 0
              ? s.blightFrames[Math.max(0, Math.min(s.blightFrames.length - 1, s.blightAnimFrameIndex))]
              : null)
          : (obs.kind === 'skeleton'
              ? (s.skeletonFrames.length > 0
                  ? s.skeletonFrames[Math.max(0, Math.min(s.skeletonFrames.length - 1, s.skeletonAnimFrameIndex))]
                  : null)
            : (obs.kind === 'heart'
                ? (s.heartFrames.length > 0
                    ? s.heartFrames[Math.max(0, Math.min(s.heartFrames.length - 1, s.heartAnimFrameIndex))]
                    : null)
                  : (s.coinFrames.length > 0
                      ? s.coinFrames[Math.max(0, Math.min(s.coinFrames.length - 1, s.coinAnimFrameIndex))]
                      : s.coinFrameName)));
      const frFull = name ? s.playerAtlas.frames[name] : undefined;
      const fr = frFull?.frame;
      const pivot = frFull?.pivot || { x: 0.5, y: 1 };
      if (fr) {
        const cfg = entitiesConfig[obs.kind];
        const destHcss = cfg.visualHeight;
        const destWcss = (fr.w / fr.h) * destHcss;
        const leftCss = dxCss - pivot.x * destWcss + obs.width * 0.5 + (cfg.offsetX || 0); // центр по хитбоксу
        const bottomCss = dyCss + obs.height + (cfg.offsetY || 0);
        const topCss = bottomCss - destHcss * pivot.y;
        const dx = Math.round(leftCss * s.scale);
        const dy = Math.round((topCss) * s.scale + offsetYpx);
        const dw = Math.max(1, Math.round(destWcss * s.scale));
        const dh = Math.max(1, Math.round(destHcss * s.scale));
        const prevSmooth = s.ctx.imageSmoothingEnabled;
        s.ctx.imageSmoothingEnabled = false;
        s.ctx.drawImage(
          s.playerSprite,
          fr.x, fr.y, fr.w, fr.h,
          dx, dy, dw, dh
        );
        s.ctx.imageSmoothingEnabled = prevSmooth;
        // Debug: entity hitbox overlay (for atlas branch)
        if (s.debugDrawHitboxes) {
          const hbDx = Math.round(dxCss * s.scale);
          const hbDy = Math.round(dyCss * s.scale + offsetYpx);
          const hbDw = Math.round(obs.width * s.scale);
          const hbDh = Math.round(obs.height * s.scale);
          s.ctx.save();
          s.ctx.globalAlpha = 0.3;
          s.ctx.fillStyle = '#ff00ff';
          s.ctx.fillRect(hbDx, hbDy, hbDw, hbDh);
          s.ctx.globalAlpha = 1;
          s.ctx.lineWidth = Math.max(1, Math.round(s.scale));
          s.ctx.strokeStyle = '#ff00ff';
          s.ctx.strokeRect(hbDx, hbDy, hbDw, hbDh);
          s.ctx.restore();
        }
        continue;
      }
    }
    // Fallback rectangle
    s.ctx.fillStyle = obs.kind === 'heart' ? '#0a0' : (obs.kind === 'coin' ? '#fc0' : 'red');
    s.ctx.fillRect(
      Math.round(dxCss * s.scale),
      Math.round(dyCss * s.scale + offsetYpx),
      Math.round(obs.width * s.scale),
      Math.round(obs.height * s.scale)
    );

    // Debug: entity hitbox overlay
    if (s.debugDrawHitboxes) {
      const dx = Math.round(dxCss * s.scale);
      const dy = Math.round(dyCss * s.scale + offsetYpx);
      const dw = Math.round(obs.width * s.scale);
      const dh = Math.round(obs.height * s.scale);
      s.ctx.save();
      s.ctx.globalAlpha = 0.3;
      s.ctx.fillStyle = '#ff00ff';
      s.ctx.fillRect(dx, dy, dw, dh);
      s.ctx.globalAlpha = 1;
      s.ctx.lineWidth = Math.max(1, Math.round(s.scale));
      s.ctx.strokeStyle = '#ff00ff';
      s.ctx.strokeRect(dx, dy, dw, dh);
      s.ctx.restore();
    }
  }
}


