import type { GameState } from './state';
import type { WorkerOutMessage } from '@/types/workerMessages';
import { groundY, speedX, gravityAccel, jumpVelocity, SPAWN_INTERVAL_MIN_SEC, SPAWN_INTERVAL_MAX_SEC, ONIGIRI_BASE_PROB, ONIGIRI_MIN_GAP_OBS, ONIGIRI_MAX_GAP_OBS, COIN_SPAWN_INTERVAL_MIN_SEC, COIN_SPAWN_INTERVAL_MAX_SEC, SKELETON_IN_NEGATIVE_PROB } from './constants';
import { SPAWN_CONFIG } from '@/config/spawn';
import { entitiesConfig } from '@/config/visual';

export function update(s: GameState, dtSec: number): void {
  if (!s.ctx) return;

  // capture previous-state snapshot for interpolation (last applied step wins)
  s.prevPlayerY = s.player.y;

  // difficulty timers
  s.elapsedSec += dtSec;
  s.noHitSec += dtSec;
  if (s.recentHitCooldownSec > 0) {
    s.recentHitCooldownSec = Math.max(0, s.recentHitCooldownSec - dtSec);
  }
  if (s.restCooldownSec > 0) {
    s.restCooldownSec = Math.max(0, s.restCooldownSec - dtSec);
  }

  function easeInOutQuad(x: number): number {
    const t = Math.max(0, Math.min(1, x));
    return t < 0.5 ? 2 * t * t : 1 - Math.pow(-2 * t + 2, 2) / 2;
  }
  function getSpawnIntervalMultiplier(): number {
    const sc = SPAWN_CONFIG.scaling;
    const tNorm = Math.max(0, Math.min(1, s.elapsedSec / sc.targetSec));
    const mTime = 1 - (1 - sc.minMultiplier) * easeInOutQuad(tNorm); // от 1 к minMultiplier
    const mCoin = 1 - sc.coinFactor * Math.max(0, Math.min(1, s.coinCount / sc.coinFullScale));
    const mStreak = 1 - sc.streakFactor * Math.max(0, Math.min(1, s.noHitSec / sc.streakFullSec));
    let m = mTime * mCoin * mStreak;
    // кламп снизу и сверху
    m = Math.max(sc.minMultiplier, Math.min(1, m));
    // анти‑фрустрация
    if (s.lifeCount <= sc.relax.whenLifeLE || s.recentHitCooldownSec > 0) {
      m *= sc.relax.relaxMultiplier;
    }
    // верхняя граница 1.3 чтобы не разносило интервал после релакса
    return Math.max(0.4, Math.min(1.3, m));
  }

  if (!s.isDying) {
    if (!s.player.isJumping && s.pendingJump) {
      s.player.isJumping = true;
      s.player.vy = -jumpVelocity;
      s.pendingJump = false;
      // уведомим главный поток о старте прыжка (для SFX)
      try {
        postMessage({ type: 'sfx', s: 'jump' } as WorkerOutMessage);
      } catch {}
      // зафиксировать фазу бега и обнулить накопление на период прыжка
      s.playerRunStartIndexAtJump = s.playerAnimFrameIndex;
      s.playerRunPhaseMsDuringJump = 0;
      s.playerJumpElapsedMs = 0;
    }
  } else {
    // во время смерти — фиксируем игрока на земле и гасим прыжок
    s.pendingJump = false;
    s.player.isJumping = false;
    s.player.vy = 0;
    s.player.y = groundY - s.player.height;
  }


  if (!s.isDying && s.player.isJumping) {
    s.player.vy += gravityAccel * dtSec;
    s.player.y += s.player.vy * dtSec;
    // «немой» прогресс бега во время прыжка
    s.playerRunPhaseMsDuringJump += dtSec * 1000;
    s.playerJumpElapsedMs = Math.min(s.playerJumpTotalMs, s.playerJumpElapsedMs + dtSec * 1000);
    if (s.player.y >= groundY - s.player.height) {
      s.player.y = groundY - s.player.height;
      s.player.vy = 0;
      s.player.isJumping = false;
      s.playerJumpElapsedMs = s.playerJumpTotalMs;
      // старт бега с фиксированного кадра run_0
      if (s.playerRunFrames.length > 0) {
        s.playerAnimFrameIndex = 0;
        s.playerAnimElapsedMs = 0;
      }
      s.playerRunPhaseMsDuringJump = 0;
    }
  }

  // Player animation update:
  // - if dying, advance dying frames (one-shot)
  // - else reward one-shot → hit one-shot → run cycle; in jump кадры выбираем в рендере
  if (s.playerSprite && s.playerAtlas) {
    if (s.isDying && s.playerDyingFrames && s.playerDyingFrames.length > 0) {
      s.playerDyingElapsedMs += dtSec * 1000;
      while (s.playerDyingElapsedMs >= s.playerAnimFrameMs) {
        s.playerDyingElapsedMs -= s.playerAnimFrameMs;
        if (s.playerDyingFrameIndex + 1 < s.playerDyingFrames.length) {
          s.playerDyingFrameIndex += 1;
        } else {
          s.playerDyingPlaying = false;
          s.playerDyingFrameIndex = s.playerDyingFrames.length - 1;
          break;
        }
      }
    } else if (s.playerRewardPlaying && s.playerRewardFrames.length > 0) {
      s.playerRewardElapsedMs += dtSec * 1000;
      while (s.playerRewardElapsedMs >= s.playerAnimFrameMs) {
        s.playerRewardElapsedMs -= s.playerAnimFrameMs;
        if (s.playerRewardFrameIndex + 1 < s.playerRewardFrames.length) {
          s.playerRewardFrameIndex += 1;
        } else {
          s.playerRewardPlaying = false;
          s.playerRewardFrameIndex = s.playerRewardFrames.length - 1;
          break;
        }
      }
    } else if (s.playerHitPlaying && s.playerHitFrames.length > 0) {
      s.playerHitElapsedMs += dtSec * 1000;
      while (s.playerHitElapsedMs >= s.playerAnimFrameMs) {
        s.playerHitElapsedMs -= s.playerAnimFrameMs;
        if (s.playerHitFrameIndex + 1 < s.playerHitFrames.length) {
          s.playerHitFrameIndex += 1;
        } else {
          s.playerHitPlaying = false;
          s.playerHitFrameIndex = s.playerHitFrames.length - 1;
          break;
        }
      }
    } else if (s.playerRunFrames.length > 0) {
      if (s.player.isJumping) {
        s.playerAnimFrameIndex = 0;
        s.playerAnimElapsedMs = 0;
      } else {
        s.playerAnimElapsedMs += dtSec * 1000;
        while (s.playerAnimElapsedMs >= s.playerAnimFrameMs) {
          s.playerAnimElapsedMs -= s.playerAnimFrameMs;
          s.playerAnimFrameIndex = (s.playerAnimFrameIndex + 1) % s.playerRunFrames.length;
        }
      }
    }
  }
  // Coin animation tick (global index for all coins) — замораживаем во время смерти
  if (!s.isDying && s.coinFrames.length > 0) {
    s.coinAnimElapsedMs += dtSec * 1000;
    while (s.coinAnimElapsedMs >= s.playerAnimFrameMs) {
      s.coinAnimElapsedMs -= s.playerAnimFrameMs;
      s.coinAnimFrameIndex = (s.coinAnimFrameIndex + 1) % s.coinFrames.length;
    }
  }
  // Heart animation tick (global index for all hearts)
  if (!s.isDying && s.heartFrames.length > 0) {
    s.heartAnimElapsedMs += dtSec * 1000;
    while (s.heartAnimElapsedMs >= s.playerAnimFrameMs) {
      s.heartAnimElapsedMs -= s.playerAnimFrameMs;
      s.heartAnimFrameIndex = (s.heartAnimFrameIndex + 1) % s.heartFrames.length;
    }
  }
  // Skeleton animation tick (global index for all skeletons)
  if (!s.isDying && s.skeletonFrames.length > 0) {
    s.skeletonAnimElapsedMs += dtSec * 1000;
    while (s.skeletonAnimElapsedMs >= s.playerAnimFrameMs) {
      s.skeletonAnimElapsedMs -= s.playerAnimFrameMs;
      s.skeletonAnimFrameIndex = (s.skeletonAnimFrameIndex + 1) % s.skeletonFrames.length;
    }
  }
  // Blight animation tick (global index for all blights)
  if (!s.isDying && (s as any).blightFrames && (s as any).blightFrames.length > 0) {
    s.blightAnimElapsedMs += dtSec * 1000;
    while (s.blightAnimElapsedMs >= s.playerAnimFrameMs) {
      s.blightAnimElapsedMs -= s.playerAnimFrameMs;
      s.blightAnimFrameIndex = (s.blightAnimFrameIndex + 1) % s.blightFrames.length;
    }
  }

  if (!s.isDying) s.spawnTimerSec += dtSec;
  if (!s.isDying && s.spawnTimerSec >= s.nextSpawnSec) {
    const mult = getSpawnIntervalMultiplier();
    // ограниченный рандом: пороги появления сердца
    const roll = Math.random();
    let bucket: 'negative' | 'collectible';
    if (s.sinceLastOnigiriCount < ONIGIRI_MIN_GAP_OBS) {
      bucket = 'negative';
    } else if (s.sinceLastOnigiriCount >= ONIGIRI_MAX_GAP_OBS) {
      bucket = 'collectible';
    } else {
      bucket = roll < ONIGIRI_BASE_PROB ? 'collectible' : 'negative';
    }

    if (bucket === 'negative' || s.lifeCount >= 3) {
      // динамическая доля скелетов: растёт по времени
      const tNorm = Math.max(0, Math.min(1, s.elapsedSec / SPAWN_CONFIG.scaling.targetSec));
      const skeletonProb = Math.max(0, Math.min(0.95, SKELETON_IN_NEGATIVE_PROB + 0.35 * tNorm));
      const isSkeleton = Math.random() < skeletonProb;

      // Глобальное правило честного зазора по времени полёта прыжка
      const airSec = Math.max(0.1, (s.playerJumpTotalMs || 0) / 1000);
      const minGapSec = Math.max(SPAWN_CONFIG.scaling.minSafeGapSec, 0.6 * airSec);
      const minTimeGapCss = Math.max(20, Math.round(speedX * minGapSec));
      // найдём последнее негативное препятствие
      let lastNeg: typeof s.entities[number] | null = null;
      for (let j = s.entities.length - 1; j >= 0; j--) {
        const e = s.entities[j]!;
        if (e.kind === 'blight' || e.kind === 'skeleton') { lastNeg = e; break; }
      }
      const minHorzGapCss = Math.max(minTimeGapCss, (lastNeg?.width ?? 0) + 12);
      const spawnX = Math.max(s.cssW, (lastNeg ? lastNeg.x + minHorzGapCss : s.cssW));

      if (isSkeleton) {
        const cfg = entitiesConfig.skeleton;
        s.entities.push({ x: spawnX, y: groundY - cfg.hitboxH, width: cfg.hitboxW, height: cfg.hitboxH, kind: 'skeleton' });
      } else {
        const cfg = entitiesConfig.blight;
        s.entities.push({ x: spawnX, y: groundY - cfg.hitboxH, width: cfg.hitboxW, height: cfg.hitboxH, kind: 'blight' });
      }
      s.sinceLastOnigiriCount += 1;

      // На высокой сложности изредка добавляем «двойной» спавн с малым зазором
      const mult = ((): number => {
        const sc = SPAWN_CONFIG.scaling;
        const tN = Math.max(0, Math.min(1, s.elapsedSec / sc.targetSec));
        const ease = tN < 0.5 ? 2 * tN * tN : 1 - Math.pow(-2 * tN + 2, 2) / 2;
        const mTime = 1 - (1 - sc.minMultiplier) * ease;
        const mCoin = 1 - sc.coinFactor * Math.max(0, Math.min(1, s.coinCount / sc.coinFullScale));
        const mStreak = 1 - sc.streakFactor * Math.max(0, Math.min(1, s.noHitSec / sc.streakFullSec));
        let m = mTime * mCoin * mStreak;
        m = Math.max(sc.minMultiplier, Math.min(1, m));
        if (s.lifeCount <= sc.relax.whenLifeLE || s.recentHitCooldownSec > 0) m *= sc.relax.relaxMultiplier;
        return Math.max(0.4, Math.min(1.3, m));
      })();
      if (mult <= 0.5 && Math.random() < 0.25) {
        // последняя вставленная — негативное препятствие; используем динамический безопасный зазор
        const last = s.entities[s.entities.length - 1];
        const airSec2 = Math.max(0.1, (s.playerJumpTotalMs || 0) / 1000);
        const minGapSec2 = Math.max(SPAWN_CONFIG.scaling.minSafeGapSec, 0.6 * airSec2) * 1.2;
        const minTimeGapCss2 = Math.max(20, Math.round(speedX * minGapSec2));
        const minHorzGapCss2 = Math.max(minTimeGapCss2, (last?.width ?? 0) + 16);
        const spawnX2 = (last ? last.x : s.cssW) + minHorzGapCss2;

        // старайтесь не дублировать тип подряд: если рандом совпал — инвертируем
        let secondIsSkeleton = Math.random() < skeletonProb;
        if (last && last.kind === 'skeleton' && secondIsSkeleton) secondIsSkeleton = false;
        if (last && last.kind === 'blight' && !secondIsSkeleton) secondIsSkeleton = true;

        if (secondIsSkeleton) {
          const cfg = entitiesConfig.skeleton;
          s.entities.push({ x: spawnX2, y: groundY - cfg.hitboxH, width: cfg.hitboxW, height: cfg.hitboxH, kind: 'skeleton' });
        } else {
          const cfg = entitiesConfig.blight;
          s.entities.push({ x: spawnX2, y: groundY - cfg.hitboxH, width: cfg.hitboxW, height: cfg.hitboxH, kind: 'blight' });
        }
      }
    } else {
      // collectible здесь — только сердце
      const cfg = entitiesConfig.heart;
      s.entities.push({ x: s.cssW, y: groundY - cfg.hitboxH, width: cfg.hitboxW, height: cfg.hitboxH, kind: 'heart' });
      s.sinceLastOnigiriCount = 0;
    }
    s.spawnTimerSec = 0;
    const sc = SPAWN_CONFIG.scaling;
    const minI = SPAWN_INTERVAL_MIN_SEC * mult;
    const maxI = SPAWN_INTERVAL_MAX_SEC * mult;
    const baseNext = minI + Math.random() * Math.max(0, (maxI - minI));
    let next = Math.max(sc.minSafeGapSec, baseNext);
    // «Окно отдыха» — реже, на высокой сложности
    const r = sc.rest;
    if (s.restCooldownSec <= 0 && mult <= r.whenMultiplierLE && Math.random() < r.probability) {
      const restGap = Math.max(sc.minSafeGapSec, sc.minSafeGapSec * r.gapMultiplier);
      next = Math.max(next, restGap);
      s.restCooldownSec = r.cooldownSec;
    }
    s.nextSpawnSec = next;
  }

  // Независимый спавн монет (может совпадать по X/Y с другими объектами) — на паузе во время смерти
  if (!s.isDying) s.coinSpawnTimerSec += dtSec;
  if (!s.isDying && s.coinNextSpawnSec <= 0) {
    s.coinNextSpawnSec = COIN_SPAWN_INTERVAL_MIN_SEC + Math.random() * (COIN_SPAWN_INTERVAL_MAX_SEC - COIN_SPAWN_INTERVAL_MIN_SEC);
  }
  if (!s.isDying && s.coinSpawnTimerSec >= s.coinNextSpawnSec) {
    const cfg = entitiesConfig.coin;
    const coinH = cfg.hitboxH;
    const apex = (jumpVelocity * jumpVelocity) / (2 * Math.abs(gravityAccel)); // высота вершины прыжка
    const bottomY = Math.max(0, groundY - apex);
    const y = Math.max(0, bottomY - coinH);
    s.entities.push({ x: s.cssW, y, width: cfg.hitboxW, height: coinH, kind: 'coin' });
    s.coinSpawnTimerSec = 0;
    s.coinNextSpawnSec = COIN_SPAWN_INTERVAL_MIN_SEC + Math.random() * (COIN_SPAWN_INTERVAL_MAX_SEC - COIN_SPAWN_INTERVAL_MIN_SEC);
  }

  if (!s.isDying) {
  for (let i = s.entities.length - 1; i >= 0; i--) {
    const obs = s.entities[i]!;
    obs.x -= speedX * dtSec;
    if (obs.x + obs.width < 0) {
      s.entities.splice(i, 1);
      continue;
    }
    if (
      obs.x < s.player.x + s.player.width &&
      obs.x + obs.width > s.player.x &&
      obs.y < s.player.y + s.player.height &&
      obs.y + obs.height > s.player.y
    ) {
      if (s.isDying) {
        // во время анимации смерти игнорируем взаимодействия
        continue;
      }
      // обработка столкновений по типу
      if (obs.kind === 'heart' || obs.kind === 'coin') {
        s.entities.splice(i, 1);
        // counters for collectibles
        if (obs.kind === 'coin') {
          s.coinCount += 1;
          s.uiDirty = true;
        } else if (obs.kind === 'heart') {
          if (s.lifeCount < 3) {
            s.lifeCount += 1;
            s.uiDirty = true;
          }
        }
        // sfx для сбора предметов
        if (obs.kind === 'coin') {
          try { postMessage({ type: 'sfx', s: 'coin' } as WorkerOutMessage); } catch {}
        } else if (obs.kind === 'heart') {
          try { postMessage({ type: 'sfx', s: 'heart' } as WorkerOutMessage); } catch {}
        }
        // trigger reward one-shot animation
        if (s.playerRewardFrames.length > 0) {
          s.playerRewardPlaying = true;
          s.playerRewardElapsedMs = 0;
          s.playerRewardFrameIndex = 0;
        }
      } else {
        s.entities.splice(i, 1);
        // collision with obstacles reduces life by 1 (not below 0)
        if (s.lifeCount > 0) {
          s.lifeCount -= 1;
          s.uiDirty = true;
        }
        // reset streak, enable relax cooldown
        s.noHitSec = 0;
        s.recentHitCooldownSec = Math.max(s.recentHitCooldownSec, SPAWN_CONFIG.scaling.relax.recentHitSecs);
        // sfx для столкновений с препятствиями (для текущего удара)
        // sfx для столкновений с препятствиями
        if (obs.kind === 'blight') {
          try { postMessage({ type: 'sfx', s: 'blight' } as WorkerOutMessage); } catch {}
        } else if (obs.kind === 'skeleton') {
          try { postMessage({ type: 'sfx', s: 'skeleton' } as WorkerOutMessage); } catch {}
        }
        // если жизни закончились — запускаем смерть (анимация + задержка gameover)
        if (s.lifeCount <= 0 && !s.isDying) {
          s.isDying = true;
          s.dyingElapsedMs = 0;
          s.gameOverSent = false;
          s.pendingJump = false;
          s.player.isJumping = false;
          s.player.vy = 0;
          s.player.y = groundY - s.player.height;
          s.playerHitPlaying = false;
          s.playerRewardPlaying = false;
          try { postMessage({ type: 'death' } as WorkerOutMessage); } catch {}
          if (s.playerDyingFrames.length > 0) {
            s.playerDyingPlaying = true;
            s.playerDyingElapsedMs = 0;
            s.playerDyingFrameIndex = 0;
          }
        } else {
          // trigger hit one-shot animation (если ещё не умер)
          if (s.playerHitFrames.length > 0) {
            s.playerHitPlaying = true;
            s.playerHitElapsedMs = 0;
            s.playerHitFrameIndex = 0;
          }
        }
      }
    }
  }
  }

  // Завершение: отправляем gameover спустя dyingDurationMs после смерти
  if (s.isDying) {
    s.dyingElapsedMs += dtSec * 1000;
    if (!s.gameOverSent && s.dyingElapsedMs >= s.dyingDurationMs) {
      s.gameOverSent = true;
      try { postMessage({ type: 'gameover' } as WorkerOutMessage); } catch {}
    }
  }
}


