import { state, applyCanvasSize } from './state';
import type { GameState, PlayerAtlas } from './state';

// (Ground pattern removed)

export async function ensurePlayerSprite(s: GameState): Promise<void> {
  if (!s.ctx) return;
  if (s.playerSprite || s.playerAtlas || s.playerSpriteLoading) return;
  if (!s.playerSpriteUrl || !s.playerAtlasUrl) return;
  s.playerSpriteLoading = true;
  try {
    const [imgRes, jsonRes] = await Promise.all([
      fetch(s.playerSpriteUrl),
      fetch(s.playerAtlasUrl),
    ]);
    if (!imgRes.ok) throw new Error('Failed to fetch player sprite');
    if (!jsonRes.ok) throw new Error('Failed to fetch player atlas');
    const [imgBlob, atlas]: [Blob, PlayerAtlas] = [await imgRes.blob(), await jsonRes.json()];
    const bmp = await createImageBitmap(imgBlob);
    // Подготовим порядок кадров по X
    const byX = (a: [string, any], b: [string, any]) => {
      const ax = a[1].frame?.x ?? 0;
      const bx = b[1].frame?.x ?? 0;
      if (ax !== bx) return ax - bx;
      const ai = parseInt(a[0].split('_')[1] || '0', 10);
      const bi = parseInt(b[0].split('_')[1] || '0', 10);
      return ai - bi;
    };

    const frames = atlas.frames || {};
    const runEntries = Object.entries(frames).filter(([k]) => k.startsWith('run_')).sort(byX);
    const jumpEntries = Object.entries(frames).filter(([k]) => k.startsWith('jump_')).sort(byX);
    const hitEntries = Object.entries(frames).filter(([k]) => k.startsWith('hit_')).sort(byX);
    const dyingEntries = Object.entries(frames).filter(([k]) => k.startsWith('dying_')).sort(byX);
    const blightEntries = Object.entries(frames).filter(([k]) => k.startsWith('blight_')).sort(byX);
    const heartEntries = Object.entries(frames).filter(([k]) => k.startsWith('heart_')).sort(byX);
    const coinEntries = Object.entries(frames).filter(([k]) => k.startsWith('coin_')).sort(byX);
    const skeletonEntries = Object.entries(frames).filter(([k]) => k.startsWith('skeleton_')).sort(byX);

    // Приоритет animations из JSON
    const pickValid = (names?: string[]) =>
      (names || []).filter((n) => !!frames[n]);

    const anims = atlas.animations || {};
    let runNames = pickValid(anims.run);
    let jumpNames = pickValid(anims.jump);
    let hitNames = pickValid(anims.hit);
    let rewardNames = pickValid(anims.reward);
    let dyingNames = pickValid((anims as any).dying);
    let blightNames = pickValid((anims as any).blight);
    let heartNames = pickValid((anims as any).heart);
    let coinName = pickValid((anims as any).coin)?.[0];
    let coinNames = pickValid((anims as any).coin);
    let skeletonNames = pickValid((anims as any).skeleton);

    if (!runNames || runNames.length === 0) runNames = runEntries.map(([k]) => k);
    if (!jumpNames || jumpNames.length === 0) jumpNames = jumpEntries.map(([k]) => k);
    if (!hitNames || hitNames.length === 0) hitNames = hitEntries.map(([k]) => k);
    if (!rewardNames || rewardNames.length === 0) rewardNames = Object.keys(frames).filter((k) => k.startsWith('reward_')).sort().map((k) => k);
    if (!dyingNames || dyingNames.length === 0) dyingNames = dyingEntries.map(([k]) => k);
    if (!blightNames || blightNames.length === 0) blightNames = blightEntries.map(([k]) => k);
    if (!heartNames || heartNames.length === 0) heartNames = heartEntries.map(([k]) => k);
    if (!coinName) coinName = coinEntries[0]?.[0];
    if (!coinNames || coinNames.length === 0) coinNames = coinEntries.map(([k]) => k);
    if (!skeletonNames || skeletonNames.length === 0) skeletonNames = skeletonEntries.map(([k]) => k);

    s.playerRunFrames = runNames;
    s.playerJumpFrames = jumpNames;
    s.playerHitFrames = hitNames;
    s.playerRewardFrames = rewardNames;
    s.playerDyingFrames = dyingNames;
    s.blightFrames = blightNames || [];
    s.heartFrames = heartNames || [];
    s.coinFrameName = coinName ?? null;
    s.coinFrames = coinNames || [];
    s.skeletonFrames = skeletonNames || [];
    s.playerSprite = bmp;
    s.playerAtlas = atlas;
  } finally {
    s.playerSpriteLoading = false;
  }
}



