// Offscreen worker entry (wiring)
import type { WorkerInMessage, WorkerOutMessage } from '@/types/workerMessages';
import { state, applyCanvasSize } from './worker/state';
import { update } from './worker/logic';
import { draw } from './worker/render';

function markReady() {
  if (state.ready) return;
  state.ready = true;
  postMessage({ type: 'ready' } as WorkerOutMessage);
}

// Timing loop (driven by main rAF) with fixed simulation step
let lastNow = 0;
let accMs = 0;
const FIXED_DT_MS = 1000 / 60;
const MAX_FRAME_DT_MS = 100; // clamp spikes; prevents huge catch-up
const MAX_CATCHUP_STEPS = 5; // avoid spiral of death

function frame(now: number) {
  if (!state.ready) return; // wait handshake
  if (!lastNow) lastNow = now;
  let dtMs = now - lastNow;
  lastNow = now;
  if (dtMs < 0) dtMs = 0;
  if (dtMs > MAX_FRAME_DT_MS) dtMs = MAX_FRAME_DT_MS;

  // fixed-timestep simulation
  accMs += dtMs;
  let steps = 0;
  while (accMs >= FIXED_DT_MS && steps < MAX_CATCHUP_STEPS) {
    update(state, FIXED_DT_MS / 1000);
    accMs -= FIXED_DT_MS;
    steps++;
  }

  state.uiElapsedMs += dtMs;
  if (state.uiDirty && state.uiElapsedMs >= state.uiIntervalMs) {
    postMessage({ type: 'ui', coinCount: state.coinCount, lifeCount: state.lifeCount } as WorkerOutMessage);
    state.uiDirty = false; state.uiElapsedMs = 0;
  }

  // draw on each rAF tick from main
  const alpha = Math.max(0, Math.min(1, accMs / FIXED_DT_MS));
  draw(state, alpha, FIXED_DT_MS / 1000);
}

onmessage = (ev: MessageEvent<WorkerInMessage>) => {
  const data = ev.data || ({} as WorkerInMessage);
  switch (data.type) {
    case 'init': {
      state.canvas = data.canvas;
      state.skyColor = typeof data.skyColor === 'string' ? data.skyColor : state.skyColor;
      state.expectResize = !!data.expectResize;
      (state as any).debugDrawHitboxes = !!(data as any).debugHitboxes;

      const wantAlpha = !!(data as any).alpha;
      state.ctx = state.canvas.getContext('2d', { alpha: wantAlpha });
      if (!state.ctx) return;
      state.ctx.imageSmoothingEnabled = false;

      (state as any).transparentBg = !!(data as any).transparentBg;

      applyCanvasSize(state, data.cssW, data.cssH, data.dpr);

      if (state.expectResize) {
        if (state.readyTimer) clearTimeout(state.readyTimer);
        state.readyTimer = setTimeout(() => { markReady(); }, 200) as unknown as number;
      } else {
        markReady();
      }
      break;
    }
    case 'resize': {
      applyCanvasSize(state, data.cssW, data.cssH, data.dpr);
      if (state.expectResize) {
        state.expectResize = false;
        if (state.readyTimer) { clearTimeout(state.readyTimer); state.readyTimer = null; }
        markReady();
      }
      break;
    }
    case 'frame': {
      frame(typeof data.now === 'number' ? data.now : performance.now());
      break;
    }
    case 'vis': {
      // сброс тайминга при смене видимости, чтобы избежать рывка
      lastNow = 0;
      accMs = 0;
      break;
    }
    case 'input': {
      if (data.tap || data.space) {
        // Принимаем прыжок только если игрок на земле и не в состоянии смерти
        if (!state.isDying && state.lifeCount > 0 && !state.player.isJumping) {
          state.pendingJump = true;
        }
      }
      break;
    }
  }
};


